import { KeyValue, isPlatformBrowser } from '@angular/common';
import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Speaker } from '@app/shared/interfaces/speaker';

@Component({
  selector: 'app-room-posters',
  templateUrl: './room-posters.component.html',
})
export class RoomPostersComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute
  ) {
  }

  isBrowser = false;
  allSessionizeData: any;
  sessionGroups = {};

  async ngOnInit() {
    this.allSessionizeData = await this.activatedRoute.snapshot.data["schedule"];
    this.sessionGroups = this.groupBy(this.allSessionizeData.sessions, 'roomId');
  }

  getSpeaker(id: string): Speaker {
    return this.allSessionizeData.speakers.find( (o: any) => o.id === id);
  }

  getCategories(arr: number[] ): string[] {
    let retArr = [];

    // Levels
    for (let i = 0; i < arr.length; i += 1) {
      let category = this.allSessionizeData.categories[0].items.find(
        (o:any) => o.id === arr[i]
      );

      if (category) {
        retArr.push(
          String(category.name)
        );
      }
    }

    // Tracks
    for (let i = 0; i < arr.length; i += 1) {
      let category = this.allSessionizeData.categories[1].items.find(
        (o:any) => o.id === arr[i]
      );

      if (category) {
        retArr.push(
          String(category.name)
        );
      }
    }

    return retArr;
  }

  getRoomNameFromId(id:string): string {
    if (!this.allSessionizeData) {
      return "";
    }

    const room: any = this.allSessionizeData.rooms.find( (o: any) => String(o.id) === id);
    const retVal = room.name.split("|")[0];

    return retVal ? retVal.trim() : "";
  }

  getRoomSponsorFromId(id:string): string {
    if (!this.allSessionizeData) {
      return "";
    }

    const room: any = this.allSessionizeData.rooms.find( (o: any) => String(o.id) === id);
    const retVal = room.name.split("|")[1];

    return retVal ? retVal.replaceAll(/\s/g, "") : "";
  }

  getLogoFromSponsorName(name: string):string {
    // warning: FRAGILE / hard-coded mapping
    const basePath = '/assets/images/sponsors/';
    let fileName = "";

    switch (name) {
      case "Delta3Consulting":
        fileName = "delta3.png";
        break;
      case "Innoventure":
        fileName = "innoventure.jpg";
        break;
      case "SourceAllies":
        fileName = "source-allies.png";
        break;
      case "ExecutiveResources":
        fileName = "executive-resources.png";
        break;
      case "FarReach":
        fileName = "far-reach.png";
        break;
      case "ColeConsulting":
        fileName = "cole-consulting.png";
        break;
      case "Turnberry":
        fileName = "turnberry.png";
        break;
      case "WeWriteCode":
        fileName = "wwc.png";
        break;
      default:
        break;
    }

    return basePath + fileName;
  }

  // lacking built-in Object.groupBy()
  groupBy(items:[], key:string):object {
    return items.reduce(
      (result, item) => ({
        ...result,
        [item[key]]: [
          ...(result[item[key]] || []),
          item,
        ],
      }),
      {},
    );
  }
}
